        <option :value="1.1">110%</option>
        <option :value="1.2">120%</option>
       </v-ons-select>
      </div>
     </ons-list-item>

     <ons-list-header>Vehicle Modifications</ons-list-header>
     <ons-list-item v-for="mod in modList" :key="mod.name">
      <div class="center">{{mod.name}}</div>
      <div class="right">
       <v-ons-select @change="updateMod(mod.selected, mod.id)" v-model="mod.selected">
        <option v-for="item in mod.availableMods" :value="item.indexId" :key="item.indexId">{{item.indexId}}</option>
       </v-ons-select>
      </div>
     </ons-list-item>
     <v-ons-list>
      <v-ons-list-header>Vehicle Colors</v-ons-list-header>
      <v-ons-list-item expandable>
        Primary Color
        <div class="expandable-content">
         <v-ons-list style="margin-top: -1.75%;" modifier="inset">
          <v-ons-list-header>Colors</v-ons-list-header>
          <v-ons-list-item v-for="color in allColors" @click="updateColor(1, color.id)" :key="color.id" modifier="longdivider"><span><i :style='color.hex' class="fa-solid fa-fill-drip"></i> {{color.name}}</span></v-ons-list-item>
         </v-ons-list>
        </div>
      </v-ons-list-item>
      <v-ons-list-item expandable>
        Secondary Color
        <div class="expandable-content">
         <v-ons-list style="margin-top: -1.75%;" modifier="inset">
          <v-ons-list-header>Colors</v-ons-list-header>
          <v-ons-list-item v-for="color in allColors" @click="updateColor(2, color.id)" :key="color.id" modifier="longdivider"><span><i :style='color.hex' class="fa-solid fa-fill-drip"></i> {{color.name}}</span></v-ons-list-item>
         </v-ons-list>
        </div>
      </v-ons-list-item>
      <v-ons-list-item expandable>
        Pearlescent Color
        <div class="expandable-content">
         <v-ons-list style="margin-top: -1.75%;" modifier="inset">
          <v-ons-list-header>Colors</v-ons-list-header>
          <v-ons-list-item v-for="color in allColors" @click="updateColor(3, color.id)" :key="color.id" modifier="longdivider"><span><i :style='color.hex' class="fa-solid fa-fill-drip"></i> {{color.name}}</span></v-ons-list-item>
         </v-ons-list>
        </div>
      </v-ons-list-item>
      <v-ons-list-item expandable>
        Wheel Color
        <div class="expandable-content">
         <v-ons-list style="margin-top: -1.75%;" modifier="inset">
          <v-ons-list-header>Colors</v-ons-list-header>
          <v-ons-list-item v-for="color in allColors" @click="updateColor(4, color.id)" :key="color.id" modifier="longdivider"><span><i :style='color.hex' class="fa-solid fa-fill-drip"></i> {{color.name}}</span></v-ons-list-item>
         </v-ons-list>
        </div>
      </v-ons-list-item>
     </v-ons-list>

     <ons-list-header>Vehicle Extras</ons-list-header>
     <ons-list-item v-for="extra in extraList" :key="extra.id">
      <div class="center">{{extra.name}}</div>
      <div class="right"><ons-switch :checked="extra.state" @click="updateExtra(extra.id, !extra.state)"></ons-switch></div>
     </ons-list-item>
    </ons-list> 
   </v-ons-page>
  </div>
  <div class="home"></div>
 </div>
</template>

<script>
 global.jQuery = require('jquery');
 var $ = global.jQuery;
 window.$ = $;

export default {
 data () {
  return {
   modList: [],
   extraList: [],
   allColors: [],
   zoomLevel: 1.0, 
  } 
 },
 methods: {
  updateMod(id, modId) {
   fetch('http://power_mechanicTablet/updateMod', {method: 'post', body: JSON.stringify({modId: modId, id: id})})
  },

  updateExtra(id, state) {
   fetch('http://power_mechanicTablet/updateExtra', {method: 'post', body: JSON.stringify({id: id, state: state})})
  },

  updateColor(id, colorID) {
   fetch('http://power_mechanicTablet/updateColor', {method: 'post', body: JSON.stringify({id: id, colorId: colorID})})
  },

  updateZoom() {
   $('#device').css('zoom', this.zoomLevel);
  } 
 },

 mounted() {
  this.listener = window.addEventListener('message', (event) => {
   const item = event.data;
   if (item.show == true) {
    this.modList = item.modList
    this.extraList = item.extraList
    this.allColors = item.allColors
    $('#device').show();
    $('#device').css('zoom', this.zoomLevel);
   }

   if (item.show == false) {
    $('#device').hide();
   }
 
   if (item.updateExtras) {
    this.extraList = item.extraList
   }
  })
 }
}
</script>

